body{
    font-family: 'Lato', sans-serif;
  }
.font-sec{
  font-family: 'Roboto Mono', monospace;
}
.nav{
  position: fixed;
  min-height: 100vh;
  &-link{
    padding: 12px 32px;
    position: relative;
    &.active::before{
      content: "";
      position: absolute;
      left: 0;
      width: 4px;
      height: 32px;
      border-left: 4px solid #198754;
    }
    &:hover{
      background: #ced4da;
    }
  }
}
.logo{
  text-indent: 101%;
  overflow: hidden;
  white-space: nowrap;
  padding: 32px;
    & a{
        display: block;
        width: 151px;
        height: 40px;
        background: url(../images/logo-ANSWER.svg) no-repeat;
        background-size: cover;
    }
}
.user{
  padding: 12px 32px;
  &-img{
    width: 40px;
    height: 40px;
    border-radius: 24px;
  }
}
.main{
  margin-left: 25%;
}
.list-style-none{
  list-style: none;
}
.fs-12px{
  font-size: 12px;
}
.fs-14px{
  font-size: 14px;
  line-height: 21px;
}
.text-gray-600{
  color: #6C757D;
}
.text-gray-900{
  color: #212529;
}
.bg-uesr1{
  background: url(../images/emilywang.jpeg) no-repeat;
  background-size: cover;
}
.bg-uesr2{
  background: url(../images/andychen.jpeg) no-repeat;
  background-size: cover;
}
.btn-padding{
  padding: 8px 32px;
}